.button {
  cursor: pointer;
  color: #fff;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

.button:hover {
  background-color: #217dbb;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

header {
  color: #fff;
  background-color: #3498db;
  padding: 20px 0;
}

header h1 {
  margin: 0;
  font-size: 24px;
}

header nav ul {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

header nav ul li {
  margin-left: 20px;
  display: inline-block;
}

header nav ul li a {
  color: #fff;
  text-decoration: none;
  transition: color .3s;
}

header nav ul li a:hover {
  color: #ddd;
}

main {
  padding: 0;
}

main h2 {
  margin-bottom: 20px;
  font-size: 2.5em;
}

main p {
  line-height: 1.5;
}

main .banner {
  color: #fff;
  text-align: center;
  background-image: url("banner.2932a991.png");
  background-size: cover;
  margin-bottom: 20px;
  padding: 100px 0;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

.container2 {
  background-color: #ecf0f1;
  border-radius: .6em;
  width: 90vw;
  max-width: 75em;
  max-height: 80vh;
  margin: auto;
  padding: 3em 1.8em;
  font-size: 16px;
  overflow-y: auto;
  box-shadow: 1.2em 2em 3em #0003;
}

.container2::-webkit-scrollbar {
  width: 6px;
}

.container2::-webkit-scrollbar-thumb {
  background-color: #3498db;
  border-radius: 3px;
}

.container2::-webkit-scrollbar-track {
  background-color: #ecf0f1;
}

.container2::-webkit-scrollbar-thumb:hover {
  background-color: #5faee3;
}

.search-container {
  grid-template-columns: 9fr 3fr;
  gap: 1.2em;
  display: grid;
}

.search-container input {
  color: #333;
  background-color: #0000;
  border: 1px solid #a0a0a0;
  border-radius: .3em;
  outline: none;
  padding: .7em;
  font-size: .9em;
}

.search-container input:focus {
  border-color: #3498db;
}

.search-container button {
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  border-radius: .3em;
  outline: none;
  font-size: .9em;
  transition: background-color .3s, letter-spacing .3s;
}

.search-container button:hover {
  letter-spacing: 5px;
  background-color: #fbfefc;
}

#result {
  color: #333;
}

.info {
  grid-template-columns: 4fr 8fr;
  align-items: center;
  margin-top: 1.2em;
  display: grid;
  position: relative;
}

.poster {
  border: 4px solid;
  border-radius: .3em;
  width: 100%;
  animation: 2s linear infinite borderRotate;
}

.container2 h2 {
  text-align: center;
  letter-spacing: .06em;
  font-size: 2.5em;
  font-weight: 600;
  animation: 2s infinite alternate shadowAnimation;
}

.rating {
  justify-content: center;
  align-items: center;
  gap: .6em;
  margin: .6em 0 .9em;
  display: flex;
}

.rating img {
  width: 2em;
}

.rating h4 {
  font-size: 2.1em;
  font-weight: 500;
  display: inline-block;
}

.details {
  color: #333;
  justify-content: center;
  gap: 1em;
  margin: .6em 0;
  font-size: 1.8em;
  font-weight: 300;
  display: flex;
}

.genre {
  justify-content: space-around;
  font-size: 2em;
  display: flex;
}

.genre div {
  border: 1px solid #a0a0a0;
  border-radius: .4em;
  padding: .4em 1.6em;
  font-size: .75em;
  font-weight: 300;
}

.container2 h3 {
  color: #333;
  margin-top: 1.2em;
  font-size: 1.4em;
  font-weight: 500;
}

.container2 p {
  text-align: justify;
  color: #333;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.8em;
}

.msg {
  text-align: center;
}

.review {
  border: 1px solid;
  border-radius: .3em;
  margin-bottom: 20px;
  padding: 10px;
  animation: 2s linear infinite borderRotate;
  display: flex;
}

.avatar {
  margin-right: 10px;
}

.avatar img {
  border-radius: 50%;
  width: 45px;
  height: 45px;
  transition: transform .5s;
}

.avatar img:hover {
  transform: rotate(360deg)scale(1.2);
}

.review-info {
  flex: 1;
}

.read-more-btn {
  color: #3498db;
  text-align: center;
  background-color: #2ecc71;
  border-radius: 5px;
  width: 25%;
  margin-top: 10px;
  padding: 8px 12px;
  text-decoration: none;
  transition: background-color .3s, width .3s;
  display: inline-block;
}

.read-more-btn:hover {
  color: #000;
  background-color: #fbfefc;
  width: 50%;
}

@media screen and (width <= 600px) {
  .container2 {
    font-size: 14px;
  }

  .info {
    grid-template-columns: 1fr;
  }

  .poster {
    width: auto;
    max-height: 10.8em;
    margin: auto;
  }

  .read-more-btn {
    width: 75%;
  }
}

.featured-movie {
  background-color: #ecf0f1;
  border-radius: 10px;
  align-items: center;
  max-width: 75rem;
  margin: 20px auto;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

.featured-movie .featured-movie-poster {
  width: 200px;
  height: auto;
  margin-right: 20px;
}

.featured-movie .featured-movie-details {
  flex: 1;
}

.featured-movie .featured-movie-details h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.featured-movie .featured-movie-details p {
  margin-bottom: 8px;
  font-size: 16px;
}

.top20-movie {
  background-color: #ecf0f1;
  border-radius: 10px;
  align-items: center;
  max-width: 50rem;
  margin: 20px auto;
  animation: 10s infinite alternate changeBackgroundColor;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

.top20-movie .top20-movie-poster {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 120px;
  height: auto;
  margin-right: 20px;
}

.top20-movie .top20-movie-details {
  flex: 1;
  padding: 10px;
}

.top20-movie .top20-movie-details h3 {
  margin-bottom: 5px;
  font-size: 18px;
}

.top20-movie .top20-movie-details p {
  color: #333;
  margin-bottom: 5px;
  font-size: 14px;
}

:root {
  --background-color: #ecf0f1;
  --text-color: #2c3e50;
}

.dark {
  --background-color: #34495e;
  --text-color: #ecf0f1;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
}

@keyframes shadowAnimation {
  0% {
    text-shadow: 2px 2px 5px #00000080;
  }

  50% {
    text-shadow: 4px 4px 8px #000c;
  }

  100% {
    text-shadow: 2px 2px 5px #00000080;
  }
}

@keyframes borderRotate {
  0% {
    border-color: red;
  }

  25% {
    border-color: #00f;
  }

  50% {
    border-color: green;
  }

  75% {
    border-color: #ff0;
  }

  100% {
    border-color: red;
  }
}

@keyframes changeBackgroundColor {
  0% {
    background-color: #767676;
  }

  50% {
    background-color: #a0a0a0;
  }

  100% {
    background-color: #e6e6e6;
  }
}

